import React, { Fragment } from "react";
import ReactWhatsapp from "react-whatsapp";

const ArtistInfo = ({ profile }) => {
  const baseUrl = "https://spacenartists.com/artist-profile/";
  const BG = {
    backgroundImage: "url(/assets/images/ProfileImgBg.png)",
  };
  const text = "Please check out the profile";
  const links = {
    whatsappLink: `https://api.whatsapp.com/send?text=${profile.description} ${baseUrl}${profile.id}`,
    facebookLink: `https://www.facebook.com/sharer.php?u=${baseUrl}${profile.id}`,
    pinterestLink: `https://pinterest.com/pin/create/bookmarklet/?media=${profile.profilePicture}&url=${baseUrl}${profile.id}&description=${profile.description}`,
    linkdinLink: `https://www.linkedin.com/shareArticle?url=${baseUrl}${profile.id}&title=${profile.description}`,
  };
  console.log("profile", profile);
  return (
    <Fragment>
      <div className="ArtistInfoDiv DFlex">
        <div className="ArtistImgDiv DBlock">
          <div className="ImgDiv DBlock" style={BG}>
            <img
              src={profile ? profile.profilePicture : ""}
              alt=""
              className=""
            />
          </div>
          <div className="ReactText DBlock d-none d-md-flex h-auto">
            <p>Reach out to me:</p>
          </div>
          <ul className="ReactLinkUl DFlex d-none d-md-flex">
            {profile &&
            profile.whatsapp !== null &&
            profile.whatsapp !== "null" &&
            profile.whatsapp !== "" ? (
              <li>
                <ReactWhatsapp
                  number={profile.whatsapp}
                  className="bg-transparent border-0"
                >
                  <a href="#!">
                    <i className="fab fa-whatsapp icon"></i>
                  </a>
                </ReactWhatsapp>
              </li>
            ) : null}
            {profile &&
            profile.facebook !== null &&
            profile.facebook !== "null" &&
            profile.facebook !== "" ? (
              <li>
                <a href={profile.facebook} target="blank">
                  <i className="fab fa-facebook-f icon"></i>
                </a>
              </li>
            ) : (
              <li>
                <a href="#!" className="disabled">
                  <i className="fab fa-facebook-f icon"></i>
                </a>
              </li>
            )}
            {profile &&
            profile.instagram !== null &&
            profile.instagram !== "null" &&
            profile.instagram !== "" ? (
              <li>
                <a href={profile.instagram} target="blank">
                  <i className="fab fa-instagram icon"></i>
                </a>
              </li>
            ) : (
              <li>
                <a href="#!" className="disabled">
                  <i className="fab fa-instagram icon"></i>
                </a>
              </li>
            )}
            {profile &&
            profile.linkedin !== null &&
            profile.linkedin !== "null" &&
            profile.linkedin !== "" ? (
              <li>
                <a href={profile.linkedin} target="blank">
                  <i className="fab fa-linkedin-in icon"></i>
                </a>
              </li>
            ) : (
              <li>
                <a href="#!" className="disabled">
                  <i className="fab fa-linkedin-in icon"></i>
                </a>
              </li>
            )}
            {profile &&
            profile.website !== null &&
            profile.website !== "null" &&
            profile.website !== "" ? (
              <li>
                <a href={profile.website} target="blank">
                  <i className="fas fa-globe icon"></i>
                </a>
              </li>
            ) : (
              <li>
                <a href="#!" className="disabled">
                  <i className="fas fa-globe icon"></i>
                </a>
              </li>
            )}
          </ul>
        </div>
        <div className="ArtistTextDiv DBlock">
          <div className="Title DBlock">
            <h3>{profile ? profile.firstName + " " + profile.lastName : ""}</h3>
            <p>
              {profile ? profile.cityName : "N/A"},{" "}
              {profile ? profile.countryName : ""}
            </p>
          </div>
          <div className="Description DBlock">
            <div className="Description DFlex justify-content-start BB">
              <h5>{profile.categoryParentName}</h5>{" "}
              <h6>{profile.categoryName}</h6>
            </div>
            <b>Description:</b>
            <p>{profile ? profile.description : ""}</p>
          </div>
          <div className="ReactText DBlock d-flex d-md-none">
            <p>Reach out to me:</p>
          </div>
          <ul className="ReactLinkUl DFlex d-flex d-md-none">
            {profile &&
            profile.whatsapp !== null &&
            profile.whatsapp !== "null" &&
            profile.whatsapp !== "" ? (
              <li>
                <ReactWhatsapp
                  number={profile.whatsapp}
                  className="bg-transparent border-0"
                >
                  <a href="#!">
                    <i className="fab fa-whatsapp icon"></i>
                  </a>
                </ReactWhatsapp>
              </li>
            ) : null}
            {profile &&
            profile.facebook !== null &&
            profile.facebook !== "null" &&
            profile.facebook !== "" ? (
              <li>
                <a href={profile.facebook} target="blank">
                  <i className="fab fa-facebook-f icon"></i>
                </a>
              </li>
            ) : (
              <li>
                <a href="#!" className="disabled">
                  <i className="fab fa-facebook-f icon"></i>
                </a>
              </li>
            )}
            {profile &&
            profile.instagram !== null &&
            profile.instagram !== "null" &&
            profile.instagram !== "" ? (
              <li>
                <a href={profile.instagram} target="blank">
                  <i className="fab fa-instagram icon"></i>
                </a>
              </li>
            ) : (
              <li>
                <a href="#!" className="disabled">
                  <i className="fab fa-instagram icon"></i>
                </a>
              </li>
            )}
            {profile &&
            profile.linkedin !== null &&
            profile.linkedin !== "null" &&
            profile.linkedin !== "" ? (
              <li>
                <a href={profile.linkedin} target="blank">
                  <i className="fab fa-linkedin-in icon"></i>
                </a>
              </li>
            ) : (
              <li>
                <a href="#!" className="disabled">
                  <i className="fab fa-linkedin-in icon"></i>
                </a>
              </li>
            )}
            {profile &&
            profile.website !== null &&
            profile.website !== "null" &&
            profile.website !== "" ? (
              <li>
                <a href={profile.website} target="blank">
                  <i className="fas fa-globe icon"></i>
                </a>
              </li>
            ) : (
              <li>
                <a href="#!" className="disabled">
                  <i className="fas fa-globe icon"></i>
                </a>
              </li>
            )}
          </ul>
        </div>
      </div>
      {/* social links */}
      <div className="row shareLinks">
        <div className=" DBlock h-auto col-md-6 ml-3">
          <p className="TitlePort titlePort-imb">Share this profile</p>
          <ul className="ReactLinkUl d-md-flex shareLinksUrl">
            {/* facebook */}
            <li>
              <a href={links.facebookLink} target="blank">
                <i className="fab fa-facebook-f icon"></i>
              </a>
            </li>
            {/* whatsapp */}
            <li>
              <a href={links.whatsappLink} target="blank">
                <i className="fab fa-whatsapp icon"></i>
              </a>
            </li>
            {/* instagrams */}
            <li>
              <a href={links.pinterestLink} target="blank">
                <i className="fab fa-pinterest icon"></i>
              </a>
            </li>
            {/* linkdin */}
            <li>
              <a href={links.linkdinLink} target="blank">
                <i className="fab fa-linkedin-in icon"></i>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </Fragment>
  );
};

export default ArtistInfo;
