import React, { useRef } from "react";
import axios from "axios";
import { Card, CardBody, CardTitle } from "reactstrap";
import { useTable, usePagination, useSortBy } from "react-table";
import ToggleButton from "react-toggle-button";
import {
  headers,
  notificationError,
  notificationSuccess,
  validateValue,
} from "../admin/constants";
const handleToggle = async (cell) => {
  const id = cell.row.original.id;
  const featured = cell.row.original.featured;
  // console.log("THIS is cell", cell.row.original.featured);
  // return;
  try {
    const res = await axios.get(`/user/featured/${id}/${featured}`, {
      headers: {
        ...headers.simple,
        authorization: localStorage.getItem("adminToken"),
      },
    });
    if (res.status === 200) {
      notificationSuccess("Featured Status Updated");
      window.location.reload();
      // setLoader(false);
      // setValues({
      //   id: res.data[0].id,
      //   name: res.data[0].category_name,
      //   isParent: res.data[0].parent_id !== 0 ? false : true,
      //   parentId: res.data[0].parent_id !== 0 ? res.data[0].parent_id : 0,
      // });
      console.log(res.data);
    }
  } catch (error) {
    notificationError("Category", error);
  }
};
const featuredToggle = (mycell, cellIndex) => {
  return (
    <ToggleButton
      value={mycell.value == 1 ? true : false}
      onToggle={() => {
        handleToggle(mycell);
        mycell.value = !mycell.value;
      }}
    />
    // Material Design example...

    // Your button can look very different, if you want.
  );
};

function Table({
  columns,
  data,
  gotoPage,
  pageCount,
  previousPage,
  nextPage,
  canPreviousPage,
  canNextPage,
  defaultPageSize = 5,
}) {
  const {
    getTableProps,
    getTableBodyProps,
    prepareRow,
    headerGroups,
    page,
    setPageSize,
    state: { pageIndex, pageSize },
  } = useTable(
    {
      columns,
      data,
      initialState: {
        pageIndex: 0,
        pageSize: defaultPageSize,
        pageCount,
        canPreviousPage,
        canNextPage,
      },
    },
    useSortBy,
    usePagination
  );
  const myRefs = useRef([]);
  return (
    <>
      <table
        {...getTableProps()}
        className="r-table table table-dark table-striped"
      >
        <thead>
          {headerGroups.map((headerGroup) => (
            <tr {...headerGroup.getHeaderGroupProps()}>
              {headerGroup.headers.map((column, columnIndex) => (
                <th
                  key={`th_${columnIndex}`}
                  {...column.getHeaderProps(column.getSortByToggleProps())}
                  className={
                    column.isSorted
                      ? column.isSortedDesc
                        ? "sorted-desc p-3"
                        : "sorted-asc p-3"
                      : " p-3"
                  }
                >
                  {column.render("Header")}
                  <span />
                </th>
              ))}
            </tr>
          ))}
        </thead>

        <tbody {...getTableBodyProps()}>
          {page.map((row) => {
            prepareRow(row);
            return (
              <tr {...row.getRowProps()}>
                {row.cells.map((cell, cellIndex) => (
                  <td
                    id={cell.id}
                    key={`td_${cellIndex}`}
                    {...cell.getCellProps({
                      className: cell.column.cellClass,
                    })}
                  >
                    {cellIndex === 4
                      ? featuredToggle(cell, cellIndex)
                      : cell.render("Cell")}
                    {/* {cell.render("Cell")} */}
                  </td>
                ))}
              </tr>
            );
          })}
        </tbody>
      </table>

      <nav aria-label="Page navigation example">
        <div className="pagination">
          <button
            onClick={() => gotoPage(0)}
            className={`page-link ${!canPreviousPage ? "disabled" : ""}`}
            disabled={!canPreviousPage}
          >
            {"<<"}
          </button>
          <button
            onClick={() => previousPage()}
            className={`page-link ${!canPreviousPage ? "disabled" : ""}`}
            disabled={!canPreviousPage}
          >
            {"<"}
          </button>
          <button
            onClick={() => nextPage()}
            className={`page-link ${!canNextPage ? "disabled" : ""}`}
            disabled={!canNextPage}
          >
            {">"}
          </button>
          <button
            onClick={() => gotoPage(pageCount - 1)}
            className={`page-link ${!canNextPage ? "disabled" : ""}`}
            disabled={!canNextPage}
          >
            {">>"}
          </button>
        </div>
      </nav>
    </>
  );
}

export const ReactTableWithPaginationCard = (props) => {
  const cols = React.useMemo(() => props.cols, []);

  return (
    <Card className="mb-4">
      <CardBody>
        <Table columns={cols} data={props.data} />
      </CardBody>
    </Card>
  );
};

export const ReactTable = (props) => {
  const cols = React.useMemo(() => props.cols, []);

  return (
    <div className="mb-4 table-responsive">
      <Table
        columns={cols}
        nextPage={props.nextPage}
        gotoPage={props.gotoPage}
        getPageData={props.getPageData}
        pageCount={props.pageCount}
        previousPage={props.previousPage}
        canPreviousPage={props.canPreviousPage}
        canNextPage={props.canNextPage}
        data={props.data}
        divided
        defaultPageSize={props.defaultPageSize}
      />
    </div>
  );
};
